@import-normalize;
@import url("https://fonts.googleapis.com/css2?family=Jost:wght@400;700&family=Rubik:wght@400;700&display=swap");
@import "variables";

body {
  position: relative;
  margin: 0;
  font-family: $body-font;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: $light-bg;
  color: $text-color;
  user-select: none;
  overflow-x: hidden;
  overflow-y: scroll;
  &.menu-is-open {
    overflow: hidden;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
  background: none;
  border: none;
  &:hover {
    cursor: pointer;
  }
  &:focus {
    outline: none;
  }
}

p {
  font-size: 18px;
  line-height: 30px;
  font-weight: 400;
}

q {
  font-weight: bold;
  font-style: italic;
  color: $green-dark;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $heading-font;
  font-weight: bold;
}

h2 {
  font-size: 62px;
  color: $green;
}

blockquote {
  font-family: $heading-font;
  font-size: 64px;
  font-weight: bold;
  font-style: italic;
  color: $green-dark;
  text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
}

figure {
  margin: 0;
}
